@import "project.less";
@import "smart-grid.less";

.debug{
    .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}


/* Defaults */
.reset();

button {
			&:focus {
						outline: none
			}
}

input, {
	&:focus {
		outline: none; 
	}
}

.wrapper {
	.wrapper();
}

.clearfix {
		display: block;
		clear: both;
}


.btn {
		display: inline-block;
		margin-top: 50px;
		width: auto;
		padding-left: 40px;
		padding-right: 40px;
		height: 50px;
		line-height: 50px;
		box-sizing: border-box;
		border-radius: 10px;
		border-bottom: 5px solid #c29631;
		.regBold();
		font-weight: bold;
		text-transform: uppercase;
		font-size: 22px;
		text-align: center;
		text-decoration: none;
		color: #333;
		background: linear-gradient(#f3c53d, #d5a12a);
		box-shadow: yellow 0px 0px 60px;
		transition: all 1s;

		&:hover {
			transform: scale(1.05);
			box-shadow: yellow 0px 0px 60px;
		}

		.xs-block({
					font-size: 18px;
					padding-left: 5px;
					padding-right: 5px;
		});
}


.container {
	max-width: 960px;
	margin: 0 auto;
}

.main {
	width: 100%;
	height: 200vh;
	background: url(../img/main/bg-main.jpg) top center no-repeat;
	background-size: cover;
	position: relative;
}

.logo {
	width: 230px;
	height: 103px;
	background: url(../img/main/logo.png) top center no-repeat;
}


.discount {
	width: 95%;
	margin: 90px auto 0;
	background: rgba(0,0,0, 0.6);
	border: 1px solid white;
	box-sizing: border-box;
	padding: 40px 40px 20px 40px;

	.xs-block({
				padding: 40px 10px 20px 10px;
				margin-top: 40px;
	});

	&-title {
		.regBold();
		font-weight: bold;
		color: white;
		font-size: 46px;
		text-align: center;

		.xs-block({
				font-size: 25px;
		});
	}

	&-btn {
		text-align: center;
		margin-bottom: 40px;
	}

	&-rules {
		.regBold();
		font-weight: bold;
		font-size: 24px;
		color: white;
		text-align: center;
		margin-bottom: 20px;
	}

	&-list {
		list-style: none;
		padding-left: 30px;
		padding-right: 30px;

		.xs-block({
				padding-left: 5px;
				padding-right: 5px;
		});

		li {
			.regBold();
			font-weight: bold;
			font-size: 22px;
			color: #fff;
			margin-bottom: 2px;

			.xs-block({
					font-size: 18px;
			});
		}
	}


}


.footer {
	width: 100%;
	height: 100px;
	background: rgba(0,0,0, 0.8);
	position: absolute;
	bottom: 0;
	left: auto;
	display: flex;
	align-items: center;
	justify-content: center;
	

	span {
		display: block;
	}

	&-text {
			.reg();
			font-size: 18px;
			color: @white;
			line-height: 27px;
			.xs-block({
					font-size: 14px;
					line-height: 20px;
					text-align: center;
			});
	}

	


}












	















































